const initState = {
  openPageFull: false,
  openSideList: false,
  index: 1,
  _nav_HoChiMinh_Viper: {
    name: 'Viper',
    url: '/HoChiMinh/Viper',
    icon: 'icon-drop',
    badge: {
      variant: 'info',
      text: '...',
    },
  },
  _nav_HoChiMinh_Quattro: {
    name: 'Quattro',
    url: '/HoChiMinh/Quattro',
    icon: 'icon-pencil',
    badge: {
      variant: 'info',
      text: '...',
    },
  },
  _nav_HoChiMinh_Traceability: {
    name: 'Traceability',
    url: '/HoChiMinh/Traceability',
    icon: 'icon-pencil',
    badge: {
      variant: 'info',
      text: '...',
    },
  },
  _nav_HoChiMinh_MobileRobot: {
    name: 'MobileRobot',
    url: '/HoChiMinh/MobileRobot',
    icon: 'icon-pencil',
    badge: {
      variant: 'info',
      text: '...',
    },
  },
  _nav_HaNoi_Viper: {
    name: 'Viper',
    url: '/HaNoi/Viper',
    icon: 'icon-drop',
    badge: {
      variant: 'info',
      text: '...',
    },
  },
  _nav_HaNoi_AnyFeeder: {
    name: 'AnyFeeder',
    url: '/HaNoi/AnyFeeder',
    icon: 'icon-pencil',
    badge: {
      variant: 'info',
      text: '...',
    },
  },
  _nav_HaNoi_Traceability: {
    name: 'Traceability',
    url: '/HaNoi/Traceability',
    icon: 'icon-pencil',
    badge: {
      variant: 'info',
      text: '...',
    },
  },
  _nav_HaNoi_MobileRobot: {
    name: 'MobileRobot',
    url: '/HaNoi/MobileRobot',
    icon: 'icon-pencil',
    badge: {
      variant: 'info',
      text: '...',
    },
  },
}
export const navReducer = (state = initState, action) => {
  switch (action.type) {
    case 'CHANGE_NAV_DATA':
      if (action.parentScada === 'HoChiMinh_Viper') {
        return {
          ...state,
          _nav_HoChiMinh_Viper: {
            name: 'Viper',
            url: '/HoChiMinh/Viper',
            icon: 'icon-drop',
            badge: {
              variant: 'info',
              text: action.status === "Connected" ? "Yes" : "No",
            },
          },
        }
      }
      if (action.parentScada === 'HoChiMinh_Quattro') {
        return {
          ...state,
          _nav_HoChiMinh_Quattro: {
            name: 'Quattro',
            url: '/HoChiMinh/Quattro',
            icon: 'icon-pencil',
            badge: {
              variant: 'info',
              text: action.status === "Connected" ? "Yes" : "No",
            },
          },
        }
      }
      if (action.parentScada === 'HoChiMinh_Traceability') {
        return {
          ...state,
          _nav_HoChiMinh_Traceability: {
            name: 'Traceability',
            url: '/HoChiMinh/Traceability',
            icon: 'icon-pencil',
            badge: {
              variant: 'info',
              text: action.status === "Connected" ? "Yes" : "No",
            },
          },
        }
      }
      if (action.parentScada === 'HoChiMinh_MobileRobot') {
        return {
          ...state,
          _nav_HoChiMinh_MobileRobot: {
            name: 'MobileRobot',
            url: '/HoChiMinh/MobileRobot',
            icon: 'icon-pencil',
            badge: {
              variant: 'info',
              text: action.status === "Connected" ? "Yes" : "No",
            },
          },
        }
      }

      if (action.parentScada === 'HaNoi_Viper') {
        return {
          ...state,
          _nav_HaNoi_Viper: {
            name: 'Viper',
            url: '/HaNoi/Viper',
            icon: 'icon-drop',
            badge: {
              variant: 'info',
              text: action.status === "Connected" ? "Yes" : "No",
            },
          },
        }
      }
      if (action.parentScada === 'HaNoi_AnyFeeder') {
        return {
          ...state,
          _nav_HaNoi_AnyFeeder: {
            name: 'AnyFeeder',
            url: '/HaNoi/AnyFeeder',
            icon: 'icon-pencil',
            badge: {
              variant: 'info',
              text: action.status === "Connected" ? "Yes" : "No",
            },
          },
        }
      }
      if (action.parentScada === 'HaNoi_Traceability') {
        return {
          ...state,
          _nav_HaNoi_Traceability: {
            name: 'Traceability',
            url: '/HaNoi/Traceability',
            icon: 'icon-pencil',
            badge: {
              variant: 'info',
              text: action.status === "Connected" ? "Yes" : "No",
            },
          },
        }
      }
      if (action.parentScada === 'HaNoi_MobileRobot') {
        return {
          ...state,
          _nav_HaNoi_MobileRobot: {
            name: 'MobileRobot',
            url: '/HaNoi/MobileRobot',
            icon: 'icon-pencil',
            badge: {
              variant: 'info',
              text: action.status === "Connected" ? "Yes" : "No",
            },
          },
        }
      }
      return {...state}
    case 'TOGGLE_FALSE_NAV_PAGE_FULL':
      console.log('navToggle ');
      return {
        ...state,
        openPageFull: false
      };
    case 'CHANGE_INDEX':
      console.log('CHANGE INDEX ');
      return {
        ...state,
        index: action.index
      };
    case 'TOGGLE_ON_OPEN_SIDELIST':
      return {
        ...state,
        openSideList: true
      };
    case 'TOGGLE_OFF_OPEN_SIDELIST':
      return {
        ...state,
        openSideList: false
      };
    default:
      return state
  }
}
