const initState = {
    editTextField: {
        id: '',
        tag: '',
        label: '',
        value: '',
        left: '',
        top: '',
        width: '',
        color: '',
        fontSize: '',
        rows: '',
    },
    addTextField: {
        tag: 0,
        label: 'label',
        value: 'value',
        left: 200,
        top: 200,
        width: 200,
        color: '#00ff00',
        fontSize: 14,
        rows: 5,
    }

}
export const textFieldReducer = (state = initState, action) => {
    switch (action.type) {
        case 'TOGGLE_TEXT_FIELD':
            return {
                ...state,
                editTextField: {
                    id: action.TextField.id,
                    tag: action.TextField.tag,
                    label: action.TextField.label,
                    value: action.TextField.value,
                    left: action.TextField.left,
                    top: action.TextField.top,
                    width: action.TextField.width,
                    color: action.TextField.color,
                    fontSize: action.TextField.fontSize,
                    rows: action.TextField.rows,
                }

            };
        case 'CHANGE_ADD_TEXT_FIELD_TAG':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    tag: action.tag,
                }
            };
        case 'CHANGE_ADD_TEXT_FIELD_LEFT':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    left: action.left,
                }
            };
        case 'CHANGE_ADD_TEXT_FIELD_TOP':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    top: action.top,
                }
            };
        case 'CHANGE_ADD_TEXT_FIELD_WIDTH':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    width: action.width,
                }
            };
        case 'CHANGE_ADD_TEXT_FIELD_COLOR':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    color: action.color,
                }
            };
        case 'CHANGE_ADD_TEXT_FIELD_FONTSIZE':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    fontSize: action.fontSize,
                }
            };
        case 'CHANGE_ADD_TEXT_FIELD_ROWS':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    rows: action.rows,
                }
            };
        case 'CHANGE_ADD_TEXT_FIELD_VALUE':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    value: action.value,
                }
            };
        case 'CHANGE_ADD_TEXT_FIELD_LABEL':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    label: action.label,
                }
            };

        case 'TEXT_FIELD_MOVE_LEFT_DECREASE':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    left: Math.round(parseInt(state.addTextField.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'TEXT_FIELD_MOVE_LEFT_INCREASE':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    left: Math.round(parseInt(state.addTextField.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'TEXT_FIELD_MOVE_TOP_DECREASE':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    top: Math.round(parseInt(state.addTextField.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'TEXT_FIELD_MOVE_TOP_INCREASE':
            return {
                ...state,
                addTextField: {
                    ...state.addTextField,
                    top: Math.round(parseInt(state.addTextField.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        //edit     
        case 'CHANGE_EDIT_TEXT_FIELD_TAG':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    tag: action.tag,
                }
            };          
        case 'CHANGE_EDIT_TEXT_FIELD_LEFT':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    left: action.left,
                }
            };
        case 'CHANGE_EDIT_TEXT_FIELD_TOP':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    top: action.top,
                }
            };
        case 'CHANGE_EDIT_TEXT_FIELD_WIDTH':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    width: action.width,
                }
            };
        case 'CHANGE_EDIT_TEXT_FIELD_COLOR':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    color: action.color,
                }
            };
        case 'CHANGE_EDIT_TEXT_FIELD_FONTSIZE':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    fontSize: action.fontSize,
                }
            };
        case 'CHANGE_EDIT_TEXT_FIELD_ROWS':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    rows: action.rows,
                }
            };
        case 'CHANGE_EDIT_TEXT_FIELD_LABEL':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    label: action.label,
                }
            };
        case 'CHANGE_EDIT_TEXT_FIELD_VALUE':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    value: action.value,
                }
            };

        case 'EDIT_TEXT_FIELD_MOVE_LEFT_DECREASE':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    left: Math.round(parseInt(state.editTextField.left) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_TEXT_FIELD_MOVE_LEFT_INCREASE':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    left: Math.round(parseInt(state.editTextField.left) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'EDIT_TEXT_FIELD_MOVE_TOP_DECREASE':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    top: Math.round(parseInt(state.editTextField.top) / parseInt(action.offset)) * parseInt(action.offset) - parseInt(action.offset),
                }
            }
        case 'EDIT_TEXT_FIELD_MOVE_TOP_INCREASE':
            return {
                ...state,
                editTextField: {
                    ...state.editTextField,
                    top: Math.round(parseInt(state.editTextField.top) / parseInt(action.offset)) * parseInt(action.offset) + parseInt(action.offset),
                }
            }
        case 'RESET_TEXT_FIELD_EDIT_FORM':
            return {
                ...state,
                editTextField: {
                    id: '',
                    tag: '',
                    label: '',
                    value: '',
                    left: '',
                    top: '',
                    width: '',
                    color: '',
                    fontSize: '',
                    rows: '',
                }
            }
        default:
            return state
    }
}